/*
Theme Name: Pixietubeuse
Author: Jérémy Young & Sandrine Martinez
Description: Thème personnalisé pour le site Pixietubeuse
Version: 1.0
*/
body {
  margin: 0;
  font-family: 'Calibri', 'Arial';
  font-size: 16px;
  color: #024269; }

header {
  background-color: #142B4D; }

header .content {
  margin: 0 auto !important; }
  @media screen and (min-width: 1200px) {
    header .content {
      width: 1000px; } }

#header-custom-image {
  display: block;
  width: 100%;
  height: auto; }

nav {
  margin-bottom: 20px;
  border-width: 0px 0px 3px 0px;
  border-style: dotted;
  border-color: #AA357A; }

nav .content {
  margin: 0 auto !important; }
  @media screen and (min-width: 1200px) {
    nav .content {
      width: 1000px; } }
  nav .content::after {
    clear: both;
    content: "";
    display: block; }

nav .menu {
  margin: 0;
  padding: 15px 0px;
  float: left; }
  nav .menu::after {
    clear: both;
    content: "";
    display: block; }

nav .menu .menu-item {
  padding-right: 30px;
  float: left;
  list-style: none;
  font-family: "Satisfy", cursive;
  font-size: 24px;
  color: #024269;
  background-image: url("images/menu_bg.svg");
  background-size: 100% 40px;
  background-repeat: no-repeat; }

nav .menu .menu-item a {
  color: #024269;
  text-decoration: none; }

nav .sub-menu {
  display: none; }

@media screen and (min-width: 1200px) {
  #pixie-sidebar {
    float: left;
    width: 250px; } }

@media screen and (min-width: 1200px) {
  .posts-list {
    float: left;
    width: 750px; } }

.post-preview {
  border-radius: 3px;
  border-color: #024269;
  border-style: solid;
  border-width: 1px; }

.post-preview-image {
  height: 250px;
  overflow: hidden;
  border-radius: 3px 3px 0px 0px; }

.post-preview-image img {
  position: relative;
  top: -25%;
  width: 100%;
  height: auto; }

.post-preview-resume {
  padding: 10px 15px; }

.post-preview-resume h2 {
  margin: 0;
  font-family: "Satisfy", cursive;
  font-size: 32px;
  color: #024269;
  text-align: center; }

.post-preview a.more-link {
  text-decoration: none; }

.post-preview a.more-link {
  display: block;
  margin: 0 auto;
  width: 150px;
  content: "Lire la suite";
  padding: 5px 20px;
  font-family: "Satisfy", cursive;
  font-size: 22px;
  text-align: center;
  color: #024269;
  background-color: #E8E9ED;
  border-radius: 3px; }

.post-preview-date-parution {
  font-family: 'Calibri', 'Arial';
  font-size: 13px;
  font-style: italic;
  text-align: center;
  color: #E7A6C5; }

footer {
  margin: 30px 0px 0px 0px;
  padding: 30px 0px;
  border-width: 3px 0px 0px 0px;
  border-style: dotted;
  border-color: #AA357A; }

footer .content {
  text-align: center;
  margin: 0 auto !important; }
  @media screen and (min-width: 1200px) {
    footer .content {
      width: 1000px; } }

footer .signature-footer {
  width: 500px; }

[id^=menu-reseaux-sociaux] ul {
  margin: 0;
  padding: 0; }

[id^=menu-reseaux-sociaux] li {
  margin-right: 10px;
  padding-right: 0px !important;
  display: inline-block;
  list-style: none;
  background-image: none !important; }

[id^=menu-reseaux-sociaux] img {
  margin-top: 5px;
  height: 25px; }

#pixie-front-page .content {
  margin: 0 auto !important; }
  @media screen and (min-width: 1200px) {
    #pixie-front-page .content {
      width: 1000px; } }
  #pixie-front-page .content::after {
    clear: both;
    content: "";
    display: block; }
